<template>
  <div class="progress-bar-wrap">
    <div class="progress-bar" role="progressbar" ref="progressBar"></div>
  </div>
</template>

<script>
export default {
  name: "ProgressCounter",
  props: {
    mivValue: {
      required: true,
      type: Number
    },
    maxValue: {
      required: true,
      type: Number
    },
  },
  data() {
    return {
      timer: null,
      currentValue: 0
    };
  },
  methods: {
    increase() {
      if (this.currentValue < this.maxValue) {
        this.currentValue++;
        this.$refs.progressBar.style.width = `${this.currentValue}%`;
      } else
        clearInterval(this.timer);
    }
  },
  mounted() {
    this.$refs.progressBar.style.width = `${this.mivValue}%`;
    this.currentValue = this.mivValue;
    const randomTime = Math.floor(Math.random() * (50 - 10 + 1) + 10);
    this.$refs.progressBar.style.transition = `${randomTime}ms`;
    this.timer = setInterval(this.increase, randomTime);
  },
  unmounted() {
    clearInterval(this.timer);
  }
}
</script>

<style scoped>
.progress-bar-wrap {
  background: rgba(255, 255, 255, 0.2);
}

.progress-bar {
  width: 1px;
  height: 10px;
  background-color: #18d26e;
}
</style>